body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.logo {
  margin-left: auto;
  margin-right: auto;
  height: 60px;
}

h4 {
  font-family: "UnifrakturCook", cursive;
  font-size: 20px;
  padding-left: 20px;
  padding-right: 20px;
  width: 200px;
  color: rgba(255, 255, 0, 0.63);
}

.alignleft {
  float: left;
  width: 33.33333%;
  text-align: left;
}

.aligncenter {
  float: left;
  width: 33.33333%;
  text-align: center;
  height: 60px;
}

.alignright {
  float: right;
  width: 33.33333%;
  text-align: right;
}

@media only screen and (max-width: 600px) {
    .aligncenter {
      display: none;
    }
    .alignleft {
        width: 50%;
      }
      .alignright {
        width: 50%;
      }
  }
.info {
    height: 200px;
    background-image: url(/clicky/static/media/info-back.8262e064.jpg);
    background-size: cover;
    background-position: center;
    margin-top: 0px;
    position: relative;
}

.middle {
    text-align: center;
    margin-top: 0px;
}

.vertical-middle {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    width: 300px;
}

.status {
    text-align: center;
}
.main-body {
  margin-top: 40px;
}
